.Header {
  height: 100vh;
  width: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  z-index: 1;
}

.Logo {
  max-width: 100%;
  height: auto;
}

.WinkLogo {
  width: 17px;
  height: 17px;
  border-radius: 50%;
  background-color: white;
  -webkit-transform: translate(3.9rem, 4rem);
      -ms-transform: translate(3.9rem, 4rem);
          transform: translate(3.9rem, 4rem);
  -webkit-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
  z-index: 20;
}

.LogoBox {
  width: auto;
  z-index: 10;
}

.LogoBox:hover .WinkLogo {
  -webkit-transform: translate(3.9rem, 4.9rem);
      -ms-transform: translate(3.9rem, 4.9rem);
          transform: translate(3.9rem, 4.9rem);
}

header h1 {
  font-family: 'Kumbh Sans', sans-serif;
  font-weight: 700;
  color: rgb(60, 51, 51);
  letter-spacing: 2px;
}

@media only screen and (max-width: 580px) {
  header h1 {
    font-size: 1.5em;
  }
}


